import { Component, OnInit } from '@angular/core';
import { InvestmentService } from '../../service/investment.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';
import { GoalService } from 'src/app/service/goal.service';

@Component({
  selector: 'app-wm-portfolio',
  templateUrl: './wm-portfolio.component.html',
  styleUrls: ['./wm-portfolio.component.css',  '../../app.component.css', '../../common/css/common.css']
})

export class WmPortfolioComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;
  wealthManagerId: any;

  constructor(private goalService: GoalService, private wcLocalStorageService: WcLocalStorageService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.getAllInfoByCustomerId(this.customerId);
  }

  public getAllInfoByCustomerId(id) {
    this.mainData = [];
    this.goalService.getAllInfoByCustomerId(id).subscribe((data: {}) => {
      this.mainData = data;
    });
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
    this.wealthManagerId = this.wcLocalStorageService.getWealthManagerId();
  }

}
