import { Component, OnInit } from '@angular/core';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-customer-homepage',
  templateUrl: './customer-homepage.component.html',
  styleUrls: ['./customer-homepage.component.css', '../../app.component.css',  '../../common/css/common.css']
})
export class CustomerHomepageComponent implements OnInit {
  
  customerId : any;
  customerName : any;

  constructor(private wcLocalStorageService: WcLocalStorageService, private router: Router) { }

  ngOnInit() {
    //TODO: to be removed
    this.wcLocalStorageService.putCustomerDetails(40001, 'Nathan Kumar');

    this.loadSessionData();
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
  }

  gotoFinancialPlan() {
    this.router.navigate(['/customer/financialplan']);
  }
  gotoPortfolio() {
    this.router.navigate(['/customer/financialplanDetail']);
  }
  gotoProfile() {
    this.router.navigate(['/customer/profile']);
  }
}
