import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { InvestmentService } from '../../service/investment.service';
import { CommonDataService } from '../../service/common-data.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

@Component({
  selector: 'app-wm-financialplan-investment-add',
  templateUrl: './wm-financialplan-investment-add.component.html',
  styleUrls: ['./wm-financialplan-investment-add.component.css',  '../../app.component.css', '../../common/css/common.css']
})

export class WmFinancialplanInvestmentAddComponent implements OnInit {

  @Input() mainData:any = {wcGoalId: 0, investmentDate: '2019-10-17', investmentAmount:100, wcWealthManagerId : 0};

  customerId : any;
  customerName : any;
  wealthManagerId: any;

  constructor(private investmentService: InvestmentService, public commonDataService: CommonDataService, private wcLocalStorageService: WcLocalStorageService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.mainData.wcGoalId = this.route.snapshot.params['id'];
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
    this.wealthManagerId = this.wcLocalStorageService.getWealthManagerId();
  }
  
  save() {
    this.investmentService.add(this.mainData).subscribe((result) => {
      this.router.navigate(['/wealthmanager/financialplanDetail/' + + this.mainData.wcGoalId]);
    }, (err) => {
      console.log(err);
    });
  }

  cancel() {
    this.router.navigate(['/wealthmanager/financialplanDetail/' + this.mainData.wcGoalId]);
  }
}