import { Component, OnInit } from '@angular/core';
import { CustomerService } from '../../service/customer.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

@Component({
  selector: 'app-customer-profile',
  templateUrl: './customer-profile.component.html',
  styleUrls: ['./customer-profile.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class CustomerProfileComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;

  constructor(private customerService: CustomerService, private wcLocalStorageService: WcLocalStorageService, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.getById(this.customerId);
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
  }

  public getById(id) {
    this.mainData = [];
    this.customerService.getById(id).subscribe((data: {}) => {
      this.mainData = data;
    });
  }

  goback() {
    this.router.navigate(['/customer/financialplan']);
  }
  
}
