import { Component, OnInit } from '@angular/core';
import { CustomerService } from '../../service/customer.service';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-wm-customer',
  templateUrl: './wm-customer.component.html',
  styleUrls: ['./wm-customer.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class WmCustomerComponent implements OnInit {

  mainData: any = [];
  
  constructor(private customerService: CustomerService, private wcLocalStorageService: WcLocalStorageService, private router: Router) { }

  ngOnInit() {
    this.getAll();
  }

  public getAll() {
    this.mainData = [];
    this.customerService.getAll().subscribe((data: {}) => {
      this.mainData = data;
    });
  }

  public selectCustomer(id, name) {
    console.log("Slect customer id-> " + id);
    console.log("Slect customer name-> " + name);

    this.wcLocalStorageService.putCustomerDetails(id,name);
    var myURL = '/wealthmanager/financialplan';
    this.router.navigate([myURL]);
  }
}
