import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { GoalService } from '../../service/goal.service';
import { CommonDataService } from '../../service/common-data.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

@Component({
  selector: 'app-wm-financialplan-detail',
  templateUrl: './wm-financialplan-detail.component.html',
  styleUrls: ['./wm-financialplan-detail.component.css',  '../../app.component.css', '../../common/css/common.css']
})

export class WmFinancialplanDetailComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;
  wealthManagerId: any;

  constructor(private goalService: GoalService, public commonDataService: CommonDataService, private wcLocalStorageService: WcLocalStorageService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.loadById();
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
    this.wealthManagerId = this.wcLocalStorageService.getWealthManagerId();
  }

  loadById() {
    console.log("load by id ---------->" + this.route.snapshot.params['id']);
    this.goalService.getInfoById(this.route.snapshot.params['id']).subscribe((data: {}) => {
      this.mainData = data;
   });
  }

  goback() {
    this.router.navigate(['/wealthmanager/financialplan']);
  }

  public addInvestment(id) {
    var myURL = '/wealthmanager/investmentAdd/' + id;
    this.router.navigate([myURL]);
  }

  public deleteGoal(id) {
    this.goalService.delete(id)
      .subscribe(res => {
        this.goback();
      }, (err) => {
          console.log(err);
        }
      );
  }
}
