 none;\n  padding: 5px 10px;\n  border-radius: 4px;\n  cursor: pointer;\n  cursor: hand;\n  font-family: Arial;\n}\nbutton:hover {\n  background-color: #cfd8dc;\n}\nbutton.delete {\n  position: relative;\n  left: 800px;\n  top: -32px;\n  background-color: gray !important;\n  color: white;\n}\n.mycard {\n  background:#fff;\n  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);\n\n  margin-left: 12px;\n  margin-bottom: 12px;\n\n  float: left;\n}\n/* On mouse-over, add a deeper shadow */\n.mycard:hover {\n  box-shadow: 0 2px 2px 0 #6a97c4;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"