import { Component, OnInit } from '@angular/core';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-wm-homepage',
  templateUrl: './wm-homepage.component.html',
  styleUrls: ['./wm-homepage.component.css',  '../../app.component.css', '../../common/css/common.css']
})
export class WmHomepageComponent implements OnInit {

  customerId : any;
  customerName : any;
  constructor(private wcLocalStorageService: WcLocalStorageService, private router: Router) { }


  ngOnInit() {
    //TODO: to be removed
    this.wcLocalStorageService.putWealthManagerId(30001);

    this.loadSessionData();
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
  }

  gotoFinancialPlan() {
    this.router.navigate(['/wealthmanager/financialplan']);
  }
  gotoPortfolio() {
    this.router.navigate(['/wealthmanager/portfolio']);
  }
  gotoProfile() {
    this.router.navigate(['/wealthmanager/profile']);
  }
  gotoCustomer() {
    this.router.navigate(['/wealthmanager/customer']);
  }
}
