import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CustomerService } from '../../service/customer.service';
import { CommonDataService } from '../../service/common-data.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-bm-customer-add',
  templateUrl: './bm-customer-add.component.html',
  styleUrls: ['./bm-customer-add.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class BmCustomerAddComponent implements OnInit {

  @Input() mainData:any = {firstName: '', lastName: '', age: 18, gender:'Male', emailId: '', city : '',  country: '', avgIncome: 10000};

  countries : any [];

  constructor(public customerService: CustomerService, public commonDataService: CommonDataService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.countries = this.commonDataService.getCountries();
  }

  save() {
    this.customerService.add(this.mainData).subscribe((result) => {
      this.router.navigate(['/businessmanager/customer']);
    }, (err) => {
      console.log(err);
    });
  }

  cancel() {
    this.router.navigate(['/businessmanager/customer']);
  }
}