import { Component, OnInit } from '@angular/core';
import { CustomerService } from '../../service/customer.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-bm-customer',
  templateUrl: './bm-customer.component.html',
  styleUrls: ['./bm-customer.component.css', '../../app.component.css', '../../common/css/common.css']
})
export class BmCustomerComponent implements OnInit {

  mainData: any = [];
  
  constructor(private customerService: CustomerService, private router: Router) { }

  ngOnInit() {
    this.getAll();
  }

  public getAll() {
    this.mainData = [];
    this.customerService.getAll().subscribe((data: {}) => {
      this.mainData = data;
    });
  }
  public add() {
    var myURL = '/businessmanager/customerAdd';
    this.router.navigate([myURL]);
  }

  public edit(id) {
    var myURL = '/businessmanager/customerEdit/' + id;
    this.router.navigate([myURL]);
  }

  public delete(id) {
    this.customerService.delete(id)
      .subscribe(res => {
          this.getAll();
        }, (err) => {
          console.log(err);
        }
      );
  }
}
