import { Component, OnInit } from '@angular/core';
import { CustomerService } from '../../service/customer.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';


@Component({
  selector: 'app-wm-profile',
  templateUrl: './wm-profile.component.html',
  styleUrls: ['./wm-profile.component.css',  '../../app.component.css', '../../common/css/common.css']
})

export class WmProfileComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;
  wealthManagerId: any;

  constructor(private customerService: CustomerService, private wcLocalStorageService: WcLocalStorageService, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.getAll();
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
    this.wealthManagerId = this.wcLocalStorageService.getWealthManagerId();
  }

  public getAll() {
    this.mainData = [];
    this.customerService.getById(this.customerId ).subscribe((data: {}) => {
      this.mainData = data;
    });
  }
}
