import { Component, OnInit } from '@angular/core';
import { GoalService } from '../../service/goal.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

@Component({
  selector: 'app-wm-financialplan',
  templateUrl: './wm-financialplan.component.html',
  styleUrls: ['./wm-financialplan.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class WmFinancialplanComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;
  wealthManagerId: any;

  constructor(private goalService: GoalService, private wcLocalStorageService: WcLocalStorageService, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.getAllInfoByCustomerId(this.customerId);
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
    this.wealthManagerId = this.wcLocalStorageService.getWealthManagerId();
  }

  public getAllInfoByCustomerId(id) {
    this.mainData = [];
    this.goalService.getAllInfoByCustomerId(id).subscribe((data: {}) => {
      this.mainData = data;
    });
  }
  public addInvestment(id) {
    var myURL = '/wealthmanager/investmentAdd/' + id;
    this.router.navigate([myURL]);
  }

  public selectGoal(id) {
    var myURL = '/wealthmanager/financialplanDetail/' + id;
    this.router.navigate([myURL]);
  }

  public addGoal() {
    var myURL = '/wealthmanager/goalAdd';
    this.router.navigate([myURL]);
  }

  public deleteGoal(id) {
    this.goalService.delete(id)
      .subscribe(res => {
        this.getAllInfoByCustomerId(this.customerId);
      }, (err) => {
          console.log(err);
        }
      );
  }

}
