import { Component, OnInit } from '@angular/core';
import { WealthmanagerService } from '../../service/wealthmanager.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-bm-wealthmanager',
  templateUrl: './bm-wealthmanager.component.html',
  styleUrls: ['./bm-wealthmanager.component.css', '../../app.component.css',  '../../common/css/common.css']
})
export class BmWealthmanagerComponent implements OnInit {

  mainData: any = [];
  
  constructor(private wealthmanagerService: WealthmanagerService, private router: Router) { }

  ngOnInit() {
    this.getAll();
  }

  public getAll() {
    this.mainData = [];
    this.wealthmanagerService.getAll().subscribe((data: {}) => {
      this.mainData = data;
    });
  }
  public add() {
    var myURL = '/businessmanager/wealthmanagerAdd';
    this.router.navigate([myURL]);
  }

  public edit(id) {
    var myURL = '/businessmanager/wealthmanagerEdit/' + id;
    this.router.navigate([myURL]);
  }

  public delete(id) {
    this.wealthmanagerService.delete(id)
      .subscribe(res => {
          this.getAll();
        }, (err) => {
          console.log(err);
        }
      );
  }
}
