import { Component, OnInit } from '@angular/core';
import { InvestmentService } from '../../service/investment.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';
import { GoalService } from 'src/app/service/goal.service';
@Component({
  selector: 'app-customer-portfolio',
  templateUrl: './customer-portfolio.component.html',
  styleUrls: ['./customer-portfolio.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class CustomerPortfolioComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;

  constructor(private goalService: GoalService, private wcLocalStorageService: WcLocalStorageService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.loadById();
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
  }

  loadById() {
    console.log("load by id ---------->" + this.route.snapshot.params['id']);
    this.goalService.getInfoById(this.route.snapshot.params['id']).subscribe((data: {}) => {
      this.mainData = data;
   });
  }
  
  goback() {
    this.router.navigate(['/customer/financialplan']);
  }
}