import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { WealthmanagerService } from '../../service/wealthmanager.service';
import { CommonDataService } from '../../service/common-data.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-bm-wealthmanager-add',
  templateUrl: './bm-wealthmanager-add.component.html',
  styleUrls: ['./bm-wealthmanager-add.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class BmWealthmanagerAddComponent implements OnInit {

  @Input() mainData:any = {firstName: '', lastName: '', age: 18, gender:'Male', emailId: '', city : '',  country: ''};

  countries : any [];

  constructor(public wealthmanagerService: WealthmanagerService, public commonDataService: CommonDataService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.countries = this.commonDataService.getCountries();
  }

  save() {
    this.wealthmanagerService.add(this.mainData).subscribe((result) => {
      this.router.navigate(['/businessmanager/wealthmanager']);
    }, (err) => {
      console.log(err);
    });
  }

  cancel() {
    this.router.navigate(['/businessmanager/wealthmanager']);
  }
}