import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CustomerService } from '../../service/customer.service';
import { CommonDataService } from '../../service/common-data.service';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-bm-customer-edit',
  templateUrl: './bm-customer-edit.component.html',
  styleUrls: ['./bm-customer-edit.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class BmCustomerEditComponent implements OnInit {

  @Input() mainData:any = {id: 0, firstName: '', lastName: '', age: 18, gender:'Male', emailId: '', city : '',  country: '', avgIncome: 10000};
  countries : any [];

  constructor(public customerService: CustomerService, public commonDataService: CommonDataService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.countries = this.commonDataService.getCountries();
    this.loadById();
  }

  loadById() {
    this.customerService.getById(this.route.snapshot.params['id']).subscribe((data: {}) => {
      console.log(data);
      this.mainData = data;
   });
  }

  save() {
    this.customerService.update(this.mainData.id, this.mainData).subscribe((result) => {
      this.router.navigate(['/businessmanager/customer']);
    }, (err) => {
      console.log(err);
    });
  }

  cancel() {
    this.router.navigate(['/businessmanager/customer']);
  }

}
