import { NgModule } from '@angular/core';
import { Routes, RouterModule, ChildrenOutletContexts } from '@angular/router';

import { ProductHomeComponent } from './product/product-home/product-home.component';
import { ProductListComponent } from './product/product-list/product-list.component';
import { ProductAddComponent } from './product/product-add/product-add.component';
import { ProductUpdateComponent } from './product/product-update/product-update.component';
import { ProductDetailComponent } from './product/product-detail/product-detail.component';

import { HomepageComponent } from './home/homepage/homepage.component';

import { CustomerHomepageComponent } from './customer/customer-homepage/customer-homepage.component';
import { CustomerFinancialplanComponent } from './customer/customer-financialplan/customer-financialplan.component';
import { CustomerPortfolioComponent } from './customer/customer-portfolio/customer-portfolio.component';
import { CustomerProfileComponent } from './customer/customer-profile/customer-profile.component';

import { WmCustomerComponent } from './wealthmanager/wm-customer/wm-customer.component';
import { WmHomepageComponent } from './wealthmanager/wm-homepage/wm-homepage.component';
import { WmFinancialplanComponent } from './wealthmanager/wm-financialplan/wm-financialplan.component';
import { WmFinancialplanDetailComponent } from './wealthmanager/wm-financialplan-detail/wm-financialplan-detail.component';
import { WmPortfolioComponent } from './wealthmanager/wm-portfolio/wm-portfolio.component';
import { WmProfileComponent } from './wealthmanager/wm-profile/wm-profile.component';
import { WmFinancialplanGoalAddComponent } from './wealthmanager/wm-financialplan-goal-add/wm-financialplan-goal-add.component';
import { WmFinancialplanInvestmentAddComponent } from './wealthmanager/wm-financialplan-investment-add/wm-financialplan-investment-add.component';

import { BmHomepageComponent } from './businessmanager/bm-homepage/bm-homepage.component';

import { BmCustomerComponent } from './businessmanager/bm-customer/bm-customer.component';
import { BmCustomerAddComponent } from './businessmanager/bm-customer-add/bm-customer-add.component';
import { BmCustomerEditComponent } from './businessmanager/bm-customer-edit/bm-customer-edit.component';

import { BmWealthmanagerAddComponent } from './businessmanager/bm-wealthmanager-add/bm-wealthmanager-add.component';
import { BmWealthmanagerEditComponent } from './businessmanager/bm-wealthmanager-edit/bm-wealthmanager-edit.component';
import { BmWealthmanagerComponent } from './businessmanager/bm-wealthmanager/bm-wealthmanager.component';

const routes: Routes = [
      { path: '',  redirectTo: '/home', pathMatch: 'full' },
      // { path: '**',  redirectTo: '/home'},
      { path: 'home',  component: HomepageComponent },
      { path: 'businessmanager',  component: BmHomepageComponent },
      { path: 'businessmanager',  
          component: BmHomepageComponent,
          children: [
            { path: '', component: BmHomepageComponent },
            { path: 'customer', component: BmCustomerComponent },
            { path: 'customerAdd', component: BmCustomerAddComponent },
            { path: 'customerEdit/:id', component: BmCustomerEditComponent },
            { path: 'wealthmanager', component: BmWealthmanagerComponent },
            { path: 'wealthmanagerAdd', component: BmWealthmanagerAddComponent },
            { path: 'wealthmanagerEdit/:id', component: BmWealthmanagerEditComponent }
          ]
      },  
      { path: 'wealthmanager', 
            component: WmHomepageComponent,
            children: [
              { path: '', redirectTo: '/wealthmanager/customer', pathMatch: 'full' },
              { path: 'customer', component: WmCustomerComponent },
              { path: 'financialplan', component: WmFinancialplanComponent },
              { path: 'financialplanDetail/:id', component: WmFinancialplanDetailComponent },
              { path: 'goalAdd', component: WmFinancialplanGoalAddComponent },
              { path: 'investmentAdd/:id', component: WmFinancialplanInvestmentAddComponent },
              { path: 'portfolio', component: WmPortfolioComponent },
              { path: 'profile', component: WmProfileComponent }
          ]
      },
      {
        path: 'customer',  
            component: CustomerHomepageComponent ,
            children: [
                { path: '', redirectTo: '/customer/financialplan', pathMatch: 'full' },
                { path: 'financialplan', component: CustomerFinancialplanComponent },
                { path: 'financialplanDetail/:id', component: CustomerPortfolioComponent },
                { path: 'profile', component: CustomerProfileComponent }
            ]
      },
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
