import { Component, OnInit } from '@angular/core';
import { GoalService } from '../../service/goal.service';
import { ActivatedRoute, Router } from '@angular/router';
import { WcLocalStorageService } from '../../service/wc-local-storage.service';

@Component({
  selector: 'app-customer-financialplan',
  templateUrl: './customer-financialplan.component.html',
  styleUrls: ['./customer-financialplan.component.css', '../../app.component.css',  '../../common/css/common.css']
})

export class CustomerFinancialplanComponent implements OnInit {

  mainData: any = [];
  customerId : any;
  customerName : any;

  constructor(private goalService: GoalService, private wcLocalStorageService: WcLocalStorageService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.loadSessionData();
    this.getAllInfoByCustomerId(this.customerId);
  }

  public loadSessionData() {
    this.customerId = this.wcLocalStorageService.getCustomerId();
    this.customerName = this.wcLocalStorageService.getCustomerName();
  }

  public getAllInfoByCustomerId(id) {
    this.mainData = [];
    this.goalService.getAllInfoByCustomerId(id).subscribe((data: {}) => {
      this.mainData = data;
    });
  }

  goback() {
    this.router.navigate(['/customer/financialplan']);
  }

  public selectGoal(id) {
    var myURL = '/customer/financialplanDetail/' + id;
    this.router.navigate([myURL]);
  }
}
